.card {
  border: 0.5px rgb(99, 99, 99) solid;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 1rem;
  position: relative;
  -webkit-box-shadow: 4px 4px 10px rgb(56, 56, 56);
          box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: all .2s ease-in-out, -webkit-transform 999s 999s;
  transition: all .2s ease-in-out, -webkit-transform 999s 999s;
  -o-transition: transform 999s 999s, all .2s ease-in-out;
  transition: transform 999s 999s, all .2s ease-in-out;
  transition: transform 999s 999s, all .2s ease-in-out, -webkit-transform 999s 999s;;
}

.card > .img-container {
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  -webkit-box-shadow: 8px 8px 20px rgb(56, 56, 56);
          box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
  -webkit-transform: rotate(5000deg);
      -ms-transform: rotate(5000deg);
          transform: rotate(5000deg);
  -webkit-transition: -webkit-transform 7s;
  transition: -webkit-transform 7s;
  -o-transition: transform 7s;
  transition: transform 7s;
  transition: transform 7s, -webkit-transform 7s;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
      -ms-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}


